import Vue from 'vue'
import App from './App'
import router from './router'
import Vant from 'vant';
import VueLazyload from 'vue-lazyload'
import 'vant/lib/vant-css/index.css';
import { AddressEdit } from 'vant';
import axios from 'axios';
import { Actionsheet } from 'vant';
import store from './store';
import { Rate } from 'vant';
import { Dialog } from 'vant';

Vue.use(Dialog);

Vue.use(Rate);


import { List } from 'vant';

import { Badge, BadgeGroup } from 'vant';
import { Progress } from 'vant';

Vue.use(Progress);

Vue.use(Badge);
Vue.use(BadgeGroup);

Vue.use(List);



Vue.use(Actionsheet);

Vue.use(AddressEdit);

Vue.use(Vant);

Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: 'static/img/imgError.jpg',
  loading: 'static/img/imgLoding.gif',
  attempt: 1
})
Vue.config.productionTip = false;
let instance = axios.create({
  // baseURL: 'http://a366e1d2.ngrok.io/',
  // baseURL: 'http://101.132.125.248:6085/',
  // baseURL: 'http://192.168.31.51:80/',
  baseURL: 'http://47.99.94.250:80/',
});
Vue.prototype.$ajax = instance;
//公共ip(未使用)
Vue.prototype.baseUrl="http://192.168.31.75:8080/fresh/";
//验证手机号
Vue.prototype.valiPhone = function(str) {
  return(/^[1][3,4,5,7,8][0-9]{9}$/.test(str));
};
/* eslint-disable no-new */
new Vue({
	el: '#app',
	router,
  store,
	components: {
		App
	},
	template: '<App/>'
})



