import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    goodsClassify: null,
    goodsList: null,
    goodsInfo: null,
    sellerInfo: null,
    dispatchingInfo: null,
    serviceInfo: null,
    roomInfo: null,
    resumeInfo: null,
    JobInfo: null,
    orderCheck: null,
  },
  mutations: {
    setGoodsClassify (state, payload) {
      state.goodsClassify  = payload
    },
    setGoodsList (state, payload) {
      state.goodsList  = payload
    },
    setGoodsInfo (state, payload) {
      state.goodsInfo  = payload
    },
    setSellerInfo (state, payload) {
      state.sellerInfo  = payload
    },
    setDispatchingInfo (state, payload) {
      state.dispatchingInfo  = payload
    },
    setServiceInfo (state, payload) {
      state.serviceInfo  = payload
    },
    setRoomInfo (state, payload) {
      state.roomInfo  = payload
    },
    setResumeInfo (state, payload) {
      state.resumeInfo  = payload
    },
    setJobInfo (state, payload) {
      state.JobInfo  = payload
    },
    setOrderCheck (state, payload) {
      state.orderCheck  = payload
    },
  }
});
export default store



